// @ts-nocheck
import React from 'react'
import Students from './students/Students';
import { BrowserRouter, Routes, Route, HashRouter } from 'react-router-dom'
import Home from './home/Home';
import Navbar from './navbar/Navbar';
import Courses from './courses/Courses';
import StuedntDetails from './students/Details';
import CourseDetails from './courses/Details';

function App() {
  return (
    <HashRouter>
      <Navbar />
      <Routes>
        <Route path='/' element={<Home />} />
        <Route path='students'>
          <Route index element={<Students />} />
          <Route path=':std_id' element={<StuedntDetails />} /> 
        </Route>
        <Route path='courses'>
          <Route index element={<Courses />} />
          <Route path=':course_Name' element={<CourseDetails />} />
        </Route>
        <Route path="*" element={<div className='container'><h1 className='mt-5'>Error 404 Page Not Found</h1></div>} />
      </Routes>
    </HashRouter>
  );
}

export default App;
